<h1>Alert 警告提示</h1>
<p>警告提示，展现需要关注的信息。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>当某个页面需要向用户显示警告的信息时。</li>
<li>非浮层的静态展现形式，始终展现，不会自动消失，用户可以点击关闭。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-alert type=\&quot;success\&quot; message=\&quot;成功提示的文案\&quot;&gt;&lt;/v-alert&gt;\n    &lt;v-alert type=\&quot;success\&quot;&gt;\n        成功提示的辅助性文字介绍成功提示的辅助性文字介绍成功提示的辅助性文字介绍成功提示的辅助性文字介绍\n    &lt;/v-alert&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-alert type="success" message="成功提示的文案"></v-alert>
    <v-alert type="success">
        成功提示的辅助性文字介绍成功提示的辅助性文字介绍成功提示的辅助性文字介绍成功提示的辅助性文字介绍
    </v-alert>
</template>
</div>
