                  <div slot="description"><h4 id="ke-guan-bi-de-jing-gao-ti-shi"><a class="header-anchor" href="#ke-guan-bi-de-jing-gao-ti-shi" aria-hidden="true">¶</a> 可关闭的警告提示</h4>
<p>可关闭的警告提示。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">message</span>=<span class="hljs-string">"错误提示的文案"</span>
    <span class="hljs-attr">description</span>=<span class="hljs-string">"错误提示的辅助性文字介绍错误提示的辅助性文字介绍错误提示的辅助性文字介绍错误提示的辅助性文字介绍错误提示的辅助性文字介绍错误提示的辅助性文字介绍"</span>
    <span class="hljs-attr">closable</span> <span class="hljs-attr">@close</span>=<span class="hljs-string">"onClose"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>: {
            onClose() {
                alert(<span class="hljs-string">"Alert Closed！"</span>)
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
